:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
:root {
  --bg-1: #07112a;
  --bg-2: #09203f;
  --gradient-a: #0366d6;
  --gradient-b: #00c2ff;
  --glass: rgba(255, 255, 255, 0.06);
  --glass-2: rgba(255, 255, 255, 0.04);
  --white: #ffffff
;
}
* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, 'Helvetica Neue', Arial;
  background: linear-gradient(180deg, var(--bg-1), var(--bg-2));
  color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 48px;
}
.bg-orb {
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 10% 20%, rgba(0, 194, 255, 0.12), transparent 10%), radial-gradient(circle at 85% 70%, rgba(3, 102, 214, 0.12), transparent 12%);
  pointer-events: none;
  filter: blur(40px);
  z-index: 0;
  animation: move 12s linear infinite;
}
.content {
  position: relative;
  z-index: 5;
  width: 100%;
  max-width: 1040px;
  padding: 36px;
}
.card {
  background: linear-gradient(180deg, var(--glass), var(--glass-2));
  backdrop-filter: blur(12px) saturate(140%);
  -webkit-backdrop-filter: blur(12px) saturate(140%);
  border-radius: 20px;
  padding: 64px;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.7), 0 0 0 1px rgba(255, 255, 255, 0.1);
  text-align: center;
}
.brand-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;
  margin-bottom: 24px;
}
.logo-wrapper {
  position: relative;
  width: 160px;
  height: 160px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: radial-gradient(circle at center, rgba(3, 102, 214, 0.15), transparent 70%);
  border-radius: 50%;
  box-shadow: 0 0 50px rgba(3, 102, 214, 0.25);
}
.logo-img {
  height: 120px;
  width: auto;
  filter: drop-shadow(0 4px 12px rgba(3, 102, 214, 0.4));
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  border-radius: 24px;
}
.logo-wrapper:hover .logo-img {
  transform: scale(1.1) rotate(-3deg);
}
.brand-text {
  text-align: left;
}
.brand {
  font-size: 5.5rem;
  margin: 0;
  line-height: 1.15;
  padding-bottom: 10px;
  font-weight: 800;
  background: linear-gradient(90deg, var(--gradient-a), var(--gradient-b));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  letter-spacing: -2px;
  text-shadow: 0 4px 12px rgba(3, 102, 214, 0.2);
}
.tagline {
  margin: 8px 0 0;
  font-size: 1.35rem;
  color: rgba(255, 255, 255, 0.95);
  font-weight: 500;
  letter-spacing: 0.5px;
}
.sub {
  margin: 12px 0 32px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.1rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}
.actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 32px;
}
.socials {
  display: flex;
  justify-content: center;
  gap: 24px;
  margin-top: 16px;
  padding-top: 24px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.social-link {
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}
.social-link:hover {
  color: var(--white);
  transform: translateY(-2px);
}
.social-icon {
  width: 24px;
  height: 24px;
}
.btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 14px 28px;
  border-radius: 12px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  border: 1px solid transparent;
  font-size: 1rem;
  transition: all 0.2s ease;
}
.btn-primary {
  background: linear-gradient(135deg, var(--gradient-a), var(--gradient-b));
  color: #051626;
  border-color: transparent;
  box-shadow: 0 4px 20px rgba(3, 102, 214, 0.25);
}
.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(3, 102, 214, 0.35);
}
.btn-ghost {
  background: rgba(255, 255, 255, 0.03);
  color: var(--white);
  border-color: rgba(255, 255, 255, 0.12);
  backdrop-filter: blur(4px);
}
.btn-ghost:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.25);
}
@keyframes move {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
@media (max-width: 880px) {
  .brand-container {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }
  .brand-text {
    text-align: center;
  }
  .card {
    padding: 40px;
  }
  .brand {
    font-size: 4rem;
  }
  .logo-wrapper {
    width: 130px;
    height: 130px;
    margin: 0 auto;
  }
  .logo-img {
    height: 95px;
  }
}
@media (max-width: 420px) {
  .card {
    padding: 24px;
  }
  .brand {
    font-size: 3rem;
  }
  .tagline {
    font-size: 1.1rem;
  }
}
#lightning-container {
  position: absolute;
  inset: 0;
  z-index: 0;
  pointer-events: none;
  overflow: hidden;
}
.lightning-flash {
  position: absolute;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.8) 0%, rgba(200, 230, 255, 0.4) 40%, transparent 70%);
  mix-blend-mode: screen;
  opacity: 0;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.lightning-bolt {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  opacity: 0;
  filter: drop-shadow(0 0 8px rgba(200, 230, 255, 0.8));
}
